import todoList from '@/view/todoList/index.vue'
import login from '@/view/login/index.vue'
import manage from '@/view/manage/index.vue'
import dashboard from '@/view/dashboard/index.vue'
import initStore from '@/view/initStore/index.vue'
import feature from '@/view/feature/index.vue'
import product from '@/view/product/index.vue'

/**
 * @meta: auth:  012 showheaderBar 
 */
let routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/todoList",
    component: todoList,
    meta: {
      title: "",
      auth: 1,
      showheaderBar: true
    }
  },
  {
    path: "/login",
    component: login,
    meta: {
      title: " - ",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/initStore",
    component: initStore,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/product",
    component: product,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/dashboard",
    component: dashboard,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: '/feature',
    component: feature,
    meta: {
      title: '',
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/manage",
    component: manage,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  }
]

export default routes



